define('dnsmon.model.row',[], function(){

    /**
     * Row is the model object for a row.
     *
     * @class Row
     * @constructor
     * @module model
     */

    var Row = function(id, label){
        this.id = id;
        this.label = label;
        this.cells = [];

        this.group = "";

        this.minimumResponseTime = null;
        this.measurementId = null;
    };

    return Row;
});
